@tailwind base;
@tailwind components;
@tailwind utilities;

@import url("https://fonts.googleapis.com/css2?family=Amatic+SC:wght@700&family=Outfit:wght@400;500;600&display=swap");

body {
  //   @apply bg-slate-300;
}
*:not(nav) > a {
  @apply text-rose-700 hover:text-rose-500;
}
nav > a {
  @apply hover:text-rose-500 font-medium;
}
h2 {
  @apply font-creative mt-16 mb-8 text-5xl text-sky-600;
}
h3 {
  @apply font-creative my-6 text-4xl text-sky-400;
}
h4 {
  @apply font-creative my-5 text-2xl text-slate-500;
}
section {
  @apply mx-auto px-4;
}
p {
  @apply text-lg leading-relaxed mb-5;
  strong {
    @apply font-bold;
  }
}
ul {
  @apply list-inside list-decimal;
}
ol {
  @apply list-inside list-disc;
}
li {
  @apply text-lg;
}
